<script>
import { randomId } from "./../../plugins/utilities";

export default {
  props: {
    id: {
      type: String,
      default: () => randomId(),
    },
    logo: {
      type: String,
      default: undefined,
    },
    heading: {
      type: [Array, String],
      default: function () {
        return [
          "IPTV-Tools by ERDesigns",
          "Leo de Bethunelaan 106",
          "9300 Aalst, Belgium",
        ];
      },
    },
    recipient: {
      type: [Array, String],
      default: "",
    },
    number: {
      type: Number,
      default: 1,
    },
    date: {
      type: [Date, String],
      default: function () {
        return new Date();
      },
    },
    dateOptions: {
      type: Object,
      default: () => {
        return { day: "2-digit", month: "long", year: "numeric" };
      },
    },
    contextItems: {
      type: Array,
      default: function () {
        return [];
      },
    },
    items: {
      type: Array,
      default: function () {
        return [];
      },
    },
    taxPercent: {
      type: [String, Number],
      default: 21,
    },
  },
  methods: {
    openContext() {
      this.$refs[this.id].open();
    },
    isDate(date) {
      return Object.prototype.toString.call(date) === "[object Date]";
    },
    formatDate(date) {
      return date.toLocaleDateString(this.$t("date.locale"), this.dateOptions);
    },
    print() {
      document.getElementById(this.id).print();
    },
    saveAsPDF(title) {
      //document.getElementById(this.id).saveAsPDF(title);
      console.log("This functionality is not implemented yet!", title);
    },
    formatCurrency(num) {
      return new Intl.NumberFormat(this.$t("date.locale"), {
        style: "currency",
        currency: "EUR",
      }).format(num);
    },
    taxOfPrice(price) {
      return (price / 100) * this.taxPercent;
    },
    parsePrice(price) {
      return parseFloat(price.replace(",", "."));
    },
  },
  computed: {
    subtotal() {
      let items = [...this.items];
      let subtotal = 0;
      for (var i = 0; i < items.length; i++) {
        subtotal +=
          (this.parsePrice(items[i].price) -
            this.taxOfPrice(this.parsePrice(items[i].price))) *
          items[i].amount;
      }
      return this.formatCurrency(subtotal);
    },
    tax() {
      let items = [...this.items];
      let tax = 0;
      for (var i = 0; i < items.length; i++) {
        tax +=
          this.taxOfPrice(this.parsePrice(items[i].price)) * items[i].amount;
      }
      return this.formatCurrency(tax);
    },
    total() {
      let items = [...this.items];
      let total = 0;
      for (var i = 0; i < items.length; i++) {
        total += this.parsePrice(items[i].price) * items[i].amount;
      }
      return this.formatCurrency(total);
    },
    erdLogo() {
      if (this.logo) {
        return this.logo;
      }
      return "data:image/svg+xml;base64,";
    },
  },
};
</script>

